import React from "react";
import classes from './DepoYear.module.scss'
import {portfolio} from "../../base"
import DepoWrapper from "../../container/DepoWrapper/DepoWrapper";

const DepoYear = props => {
  const arrYears = portfolio[0].deposit;

  return (
	  <div className={classes.DepoYear}>
		{
		  arrYears.map((obj, index) => {
			const year = Object.keys(obj);

			return <DepoWrapper year={year} index={index} key={index}/>
		  })
		}
	  </div>
  )
};

export default DepoYear;